import { Injectable } from '@angular/core';
import { Title, Meta } from '@angular/platform-browser';

@Injectable()
export class SEOServiceService {

	constructor(private titleService: Title, private metaService: Meta) { }

	// update seo details
	updateSEODetails(pageTitle, metaObject) {
		// seo object update..
		this.metaService.updateTag({ property: 'og:title', content: metaObject["ogTitle"] })
		this.metaService.updateTag({ property: 'og:description', content: metaObject["ogDescription"] })
		this.metaService.updateTag({ name: 'description', content: metaObject["description"] })

		// seo object title..
		this.titleService.setTitle(pageTitle)
	}

}
