import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { environment } from '../../../environments/environment';
import { AdminService } from '../admin.service';
import { AdminAuthService } from '../admin-auth.service';

@Component({
	selector: 'app-login',
	templateUrl: './login.component.html',
	styleUrls: ['./login.component.css']
})

export class LoginComponent implements OnInit {
	innerHeight: any = '';
	hide: boolean = true;
	captechBasURL = environment.captechBaseURL; // captech base URL..
	isLoading:boolean = false;

	constructor(
		private router: Router,
		private adminService: AdminService,
		private authService: AdminAuthService
	) { }

	ngOnInit() {
		this.authService.clearSession();
		this.innerHeight = window.innerHeight;
	}

	// get JWT token on login button click..
	loginUser(email: string, password: string) {
		this.isLoading = true;
		this.adminService.authenticateUser('authenticate/', email, password).subscribe(res => {
			if (res) {
				console.log('res ', res);
				this.authService.setTokenInSession(res.access_token, res.refresh_token);
				console.log('user Token ', sessionStorage.getItem('user'));
				// redirect to ..
				setTimeout(()=>{
					this.isLoading= false;
					this.router.navigate(['sub-contractors/', 'successfull'])
				}, 2000);
					
			} else {
				console.log('Oops something went wrong');
				this.isLoading= false;
			}
		}, error => {
			console.log('You are not authorised', error);
			this.isLoading= false;
		})
	}
}
