module.exports = ".founder-bg {\r\n    padding-bottom: 100px;\r\n}\r\n\r\n.page-images {\r\n    background: url('leader-page.jpg');\r\n    background-position: center;\r\n}\r\n\r\n.team-title {\r\n    font-size: 20px;\r\n    font-weight: 600;\r\n    margin: 0 0 30px 0;\r\n}\r\n\r\n.team-title span {\r\n    border-bottom: 2px solid #f47d35;\r\n    padding-bottom: 5px;\r\n}\r\n\r\n.team-profile {\r\n    margin: 50px 0 100px 0;\r\n    border: 1px solid rgba(128, 127, 128, 0.1);\r\n}\r\n\r\n.team-description {\r\n    font-size: 14px;\r\n    line-height: 150%;\r\n    margin: 0 0 50px 94px;\r\n    font-weight: 600;\r\n    color: #000000;\r\n}\r\n\r\n.team-profile {\r\n    margin-bottom: 50px;\r\n    text-transform: capitalize;\r\n    padding: 20px;\r\n    -webkit-border-top-left-radius: 20px;\r\n    -webkit-border-bottom-right-radius: 20px;\r\n    -moz-border-radius-topleft: 20px;\r\n    -moz-border-radius-bottomright: 20px;\r\n    border-top-left-radius: 20px;\r\n    border-bottom-right-radius: 20px;\r\n    /* -webkit-box-shadow: 10px 10px 21px -6px rgba(148, 139, 148, 1);\r\n    -moz-box-shadow: 10px 10px 21px -6px rgba(148, 139, 148, 1);\r\n    box-shadow: 10px 10px 21px -6px rgba(148, 139, 148, 1); */\r\n    -webkit-transform: scale(1);\r\n    transform: scale(1);\r\n    transition: all 0.2s ease;\r\n    box-shadow: 0 22px 20px -20px #8198b4;\r\n    border: 1px solid rgba(128, 127, 128, 0.2);\r\n}\r\n\r\n.team-profile:hover {\r\n    -webkit-transform: scale(1.02);\r\n    transform: scale(1.02);\r\n    transition: all 0.2s ease;\r\n}\r\n\r\n.team-profile .team-img {\r\n    display: inline-block;\r\n    overflow: hidden;\r\n    width: 200px;\r\n    border-radius: 100%;\r\n    height: 200px;\r\n    border: 1px solid rgba(128, 127, 128, 0.5);\r\n}\r\n\r\n.team-profile .team-img img {\r\n    width: 100%\r\n}\r\n\r\n.team-profile .name-and-description-wrapper {\r\n    display: inline-block;\r\n    vertical-align: top;\r\n    margin-left: 30px;\r\n    width: 75%\r\n}\r\n\r\n.team-profile .name-wrapper {\r\n    font-size: 18px;\r\n    font-weight: 600;\r\n    letter-spacing: 0.02rem;\r\n    margin-bottom: 5px;\r\n}\r\n\r\n.team-profile .designation-wrapper {\r\n    font-size: 13px;\r\n    letter-spacing: 0.02rem;\r\n    margin-bottom: 20px\r\n}\r\n\r\n.team-profile .team-quote {\r\n    font-size: 14px;\r\n    line-height: 150%;\r\n    font-weight: 500;\r\n    color: #000000;\r\n}\r\n\r\n.social-link {\r\n    margin-top: 10px\r\n}\r\n\r\n.social-link .link {\r\n    display: inline-block;\r\n    width: 30px;\r\n    margin-bottom: 10px;\r\n    margin-right: 10px\r\n}\r\n\r\n.social-link .link img {\r\n    width: 100%\r\n}\r\n\r\n.social-link .view-profile {\r\n    display: block;\r\n    font-size: 14px;\r\n    font-weight: 500;\r\n    color: #000000;\r\n    text-decoration: none;\r\n}\r\n\r\n.social-link .view-profile:hover {\r\n    text-decoration: underline;\r\n}\r\n\r\n@media screen and (max-width: 1199px) {\r\n    .team-profile .name-and-description-wrapper {\r\n        width: 70%\r\n    }\r\n}\r\n\r\n@media screen and (max-width: 991px) {\r\n    .team-profile .team-img {\r\n        width: 170px;\r\n        height: 170px\r\n    }\r\n    .team-profile .name-and-description-wrapper {\r\n        width: 68%;\r\n        margin-left: 20px;\r\n    }\r\n}\r\n\r\n@media screen and (max-width: 767px) {\r\n    .team-profile .team-img {\r\n        margin: 0 auto;\r\n        display: block;\r\n        margin-bottom: 20px;\r\n    }\r\n    .team-profile .name-and-description-wrapper {\r\n        width: 100%;\r\n        margin-left: 0;\r\n    }\r\n    .team-profile .name-wrapper,\r\n    .team-profile .designation-wrapper,\r\n    .social-link {\r\n        text-align: center;\r\n    }\r\n    .team-profile .team-quote {\r\n        text-align: justify;\r\n    }\r\n}\r\n\r\n@media screen and (max-width: 400px) {\r\n    .founder-bg {\r\n        padding-bottom: 150px;\r\n    }\r\n}"