import { Component, OnInit, ViewChild } from '@angular/core';
import { AdminService } from '../../admin.service';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';


@Component({
	selector: 'app-user',
	templateUrl: './user.component.html',
	styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {
	usersData: any = [];
	usersDisplayedColumns: any = ['first_name', 'last_name', 'user_name', 'role', 'action'];
	userDataLength: number = 0;
	isLoading: boolean = false;

	// pagination
	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;

	constructor(private adminService: AdminService, private router: Router) { }

	ngOnInit() {
		// get all user list..
		this.getUserList()
	}

	// get user list
	getUserList() {
		this.isLoading = true;
		this.adminService.getUserList('get-users/').subscribe(res => {
			console.log('res ', res);
			//this.usersData = res;
			this.loadDataTable(res)
			this.isLoading = false;
		}, error => {
			this.isLoading = false;
		})
	}

	// load data tables..
	loadDataTable(dataSource) {
		this.usersData = new MatTableDataSource<any>(dataSource);
		// bind data sounrce to pagination
		this.usersData.paginator = this.paginator;
		this.usersData.sort = this.sort;
		this.userDataLength = this.usersData.data.length;		
	}

	// delete user..
	deleteUser(userID){
		this.isLoading = true;
		this.adminService.deleteUser('manage-user/' + userID).subscribe(res => {
			this.getUserList();
			this.isLoading = false;
		}, error => {
			this.isLoading = false;
		});
	}

	editUser(id){
		console.log('ID ', id);
		this.router.navigate(['update-user/', id])
	}
}
