import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/admin/admin.service';

@Component({
	selector: 'app-gst-check',
	templateUrl: './gst-check.component.html',
	styleUrls: ['./gst-check.component.css']
})
export class GstCheckComponent implements OnInit {
	gstCard: string = '';
	response: string = '';
	responseGSTNo: string = '';
	responseGSTName: string = '';
	responseGSTStatus: string = '';
	errorRes: string = '';
	isLoading: boolean = false;

	constructor(private adminService: AdminService) { }

	ngOnInit() {
	}

	getGSTDetails() {		
		if (this.gstCard.length == 15) {
			this.isLoading = true;
			this.adminService.pangstCheck('verify-gst-prod/', { gst_no: this.gstCard }).subscribe(res => {
				this.response = res;
				this.responseGSTNo = res.result.gstin;
				this.responseGSTName = res.result.tradeNam;
				this.responseGSTStatus = res.result.sts;
				this.isLoading = false;
			})
		} else {
			this.errorRes = 'Please enter valid gst no.'
		}
	}

}
