import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// web component..
import { HomeComponent } from './home/home.component';
import { UnderConstructionComponent } from './under-construction/under-construction.component';
import { OurAppComponent } from './our-app/our-app.component';
import { PreRegdGcComponent } from './pre-regd-gc/pre-regd-gc.component';
import { PreRegdLcComponent } from './pre-regd-lc/pre-regd-lc.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { FounderComponent } from './founder/founder.component';
import { PrivacyPolicyComponent } from './privacy-policy/privacy-policy.component';
import { TermsConditionsComponent } from './terms-conditions/terms-conditions.component';
import { OurAssociateComponent } from './our-associate/our-associate.component';
import { MediaCoverageComponent } from './media-coverage/media-coverage.component';
import { LabourContractorRegistrationComponent } from './labour-contractor-registration/labour-contractor-registration.component';
import { ThankYouComponent } from './thank-you/thank-you.component';
import { BackofficeComponent } from './backoffice/backoffice.component';
import { SuccessStoryComponent } from './successstory/successstory.component';


// layout component..
import { WebLayoutComponent } from './layouts/web-layout/web-layout.component';
import { AdminLayoutComponent } from './layouts/admin-layout/admin-layout.component';

// admin components..
import { LoginComponent } from './admin/login/login.component';
import { DashboardComponent } from './admin/dashboard/dashboard.component';
import { UserComponent } from './admin/dashboard/user/user.component';
import { UserCrudComponent } from './admin/dashboard/user/user-crud/user-crud.component';
import { LabourRegistrationListComponent } from './admin/dashboard/labour-registration/labour-ragistration-list/labour-registration-list.component';
import { LabourRegistrationDetailComponent } from './admin/dashboard/labour-registration/labour-registration-detail/labour-registration-detail.component';
import { PanCheckComponent } from './admin/dashboard/document-check/pan-check/pan-check.component';
import { GstCheckComponent } from './admin/dashboard/document-check/gst-check/gst-check.component';
import { ExceptionErrorsComponent } from './admin/dashboard/error-log/exception-errors/exception-errors.component';
import { VarificationApiErrorComponent } from './admin/dashboard/error-log/varification-api-error/varification-api-error.component';
import { LogoutComponent } from './admin/logout/logout.component';
import { DailyReportsComponent } from './admin/dashboard/reports/daily-report/daily-report.component';
import { BpoReportComponent } from './admin/dashboard/reports/bpo-report/bpo-report.component';

const appRoutes: Routes = [

    // Web layout..
    {
        path: '', component: WebLayoutComponent, children:
            [
                { path: '', redirectTo: '/home', pathMatch: 'full' },
                {
                    path: 'home', component: HomeComponent, data:
                    {
                        title: 'Cap-tech Eforce connects general contractors with labour contractors and vendors',
                        description: 'Cap-tech India launches Eforce, an online platform that connects general contractors with labour contractors and special vendors. Learn more!',
                        ogTitle: "Cap-tech Eforce connects general contractors with labour contractors and vendors",
                        ogDescription: "Cap-tech India launches Eforce, an online platform that connects general contractors with labour contractors and special vendors. Learn more!"
                    }
                },
                {
                    path: 'our-associate-company', component: OurAssociateComponent, data:
                    {
                        title: 'Our Associate Company',
                        description: 'Cap-tech India launches Eforce, an online platform that connects general contractors with labour contractors and special vendors.',
                        ogTitle: "Our Associate Company",
                        ogDescription: "Cap-tech India launches Eforce, an online platform that connects general contractors with labour contractors and special vendors."
                    }
                },
                {
                    path: 'founder', component: FounderComponent, data:
                    {
                        title: 'Founder',
                        description: 'Asutosh Katyal, Founder of Cap-tech India launches Eforce, an online platform that connects general contractors with labour contractors.',
                        ogTitle: "Founder",
                        ogDescription: "Asutosh Katyal, Founder of Cap-tech India launches Eforce, an online platform that connects general contractors with labour contractors."
                    }
                },
                {
                    path: 'pre-regd-general-contractor', component: PreRegdGcComponent, data:
                    {
                        title: 'Pre-registration for General Contractor',
                        description: 'Captech Technologies India launches Eforce, an online platform that connects general contractors with labour contractors and special vendors.',
                        ogTitle: "Pre-registration for General Contractor",
                        ogDescription: "Captech Technologies India launches Eforce, an online platform that connects general contractors with labour contractors and special vendors."
                    }
                },
                {
                    path: 'media-coverage', component: MediaCoverageComponent, data:
                    {
                        title: 'Media Coverage',
                        description: 'Cap-tech India launches Eforce, an online platform that innovates to connect general contractors with labour contractors and special vendors.',
                        ogTitle: "Media Coverage",
                        ogDescription: "Cap-tech India launches Eforce, an online platform that innovates to connect general contractors with labour contractors and special vendors."
                    }
                },
                { path: 'testimonial', component: SuccessStoryComponent, data: { title: 'Testimonial' } },
                {
                    path: 'contact-us', component: ContactUsComponent, data:
                    {
                        title: 'Contact Us',
                        description: 'Feel Free to Contact Cap-tech India!! General contractors and labour contractors can contact for Eforce enquiry through this form of Captech Technologies pvt ltd.',
                        ogTitle: "Contact Us",
                        ogDescription: "Feel Free to Contact Cap-tech India!! General contractors and labour contractors can contact for Eforce enquiry through this form of Captech Technologies pvt ltd."
                    }
                },
                { path: 'privacy-policy', component: PrivacyPolicyComponent, data: { title: 'Privacy Policy' } },
                { path: 'terms-conditions', component: TermsConditionsComponent, data: { title: 'Terms Conditions' } },                
                { path: 'sub-contractor-registration/:src/:lang', component: LabourContractorRegistrationComponent, data: 
                    { 
                        title : 'Sub Contractor Registration',
                        description: 'Cap-tech India launches Eforce, an online platform that connects general contractors with labour contractors and special vendors.',
                        ogTitle: "Sub Contractor Registration",
                        ogDescription: "Cap-tech India launches Eforce, an online platform that connects general contractors with labour contractors and special vendors."
                    } 
                },
                { path: 'thank-you', component: ThankYouComponent, data: { title: 'Sub Contractor Registration Thank You' } },
                { path: 'send-sms', component: BackofficeComponent }
            ]
    },

    // admin layout..
    {
        path: '', component: AdminLayoutComponent, children:
            [
                { path: 'dashboard', component: DashboardComponent },
                {
                    path: '', component: DashboardComponent, children:
                        [
                            { path: 'sub-contractors/:flag', component: LabourRegistrationListComponent },
                            { path: 'sub-contractors/:flag/:id', component: LabourRegistrationDetailComponent },
                            { path: 'view-users', component: UserComponent },
                            { path: 'add-user/:id', component: UserCrudComponent },
                            { path: 'update-user/:id', component: UserCrudComponent },
                            { path: 'pan-check', component: PanCheckComponent },
                            { path: 'gst-check', component: GstCheckComponent },
                            { path: 'api-exceptions-errors', component: ExceptionErrorsComponent },
                            { path: 'verification-api-errors', component: VarificationApiErrorComponent },
                            { path: 'daily-reports', component: DailyReportsComponent }
                        ]
                },
                { path: 'logout', component: LogoutComponent }

            ]
    },

    { path: 'login', component: LoginComponent },
    { path: 'bpo-report', component: BpoReportComponent },
    { path: '**', redirectTo: '/home', pathMatch: 'full' }
];

@NgModule({
    imports: [RouterModule.forRoot(appRoutes, { scrollPositionRestoration: 'enabled' })],
    exports: [RouterModule]
})
export class AppRoutingModule { }

export const routingComponent = [
    HomeComponent, PreRegdGcComponent, PreRegdLcComponent, ContactUsComponent, FounderComponent,
    PrivacyPolicyComponent, TermsConditionsComponent, OurAssociateComponent, MediaCoverageComponent,
    LabourContractorRegistrationComponent, ThankYouComponent, BackofficeComponent, LoginComponent,
    DashboardComponent, WebLayoutComponent, AdminLayoutComponent, LabourRegistrationListComponent,
    LabourRegistrationDetailComponent, UserComponent, UserCrudComponent, PanCheckComponent, GstCheckComponent, LogoutComponent, ExceptionErrorsComponent,
    VarificationApiErrorComponent, BpoReportComponent, DailyReportsComponent, SuccessStoryComponent
]
