import { Component, OnInit, ViewChild } from '@angular/core';
import { AdminService } from 'src/app/admin/admin.service';
import { NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
	selector: 'app-user-crud',
	templateUrl: './user-crud.component.html',
	styleUrls: ['./user-crud.component.css']
})
export class UserCrudComponent implements OnInit {
	hide: boolean = true;
	userID: number;
	@ViewChild('userForm', null) userForm: NgForm;
	crudStatus: string = '';
	userName: string = '';
	isLoading: boolean = false;

	constructor(private adminService: AdminService, private route: ActivatedRoute, private router: Router) { }

	ngOnInit() {
		// get query param..
		this.route.params.subscribe(param => {
			//console.log('Param ', param);
			this.userID = param.id

			// get user details if id != 0
			if (this.userID != 0) {
				this.getUser(this.userID);
				
			}

			console.log('user id ', this.userID);
		});
	}

	// submit form..
	userSubmit(form: NgForm) {
		if (form.valid) {
			this.isLoading = true;
			if (this.userID != 0) {
				let userBody = {
					first_name: form.value.first_name,
					last_name: form.value.last_name,
					password: form.value.password,
					role: form.value.role
				}
				this.adminService.patchUser('manage-user/' + this.userID, userBody).subscribe(res => {
					console.log('Update User ', res);
					this.isLoading = false;
					this.crudStatus = "User updated succesfully";
				}, error => {
					this.isLoading = false;
				})
			} else {
				this.adminService.addUser('create-user/', form.value).subscribe(res => {
					console.log('Add user ', res);
					this.isLoading = false;
					this.crudStatus = "User created succesfully";
					this.router.navigate(['view-users/']);
				}, error => {
					this.isLoading = false;
				})
			}
		}
	}

	getUser(id) {
		this.isLoading = true;
		this.adminService.getSingleUser('manage-user/' + id).subscribe(res => {
			const formObj = {
				first_name: res.first_name,
				last_name: res.last_name,
				user_name: res.user_name,
				password: '',
				role: res.role
			}
			this.userName = res.first_name + ' ' + res.last_name;
			console.log('res ', res);
			setTimeout(() => {
				this.userForm.setValue(formObj);
				this.isLoading = false;
			});
		})
	}

	resetField() {
		this.userForm.reset();
	}

	back(){
		this.router.navigate(['/view-users'])
	}
}
