import { Component, OnInit, ViewChild } from '@angular/core';
import { AdminService } from '../../../admin.service';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';


@Component({
	selector: 'app-reports',
	templateUrl: './daily-report.component.html',
	styleUrls: ['./daily-report.component.css']
})
export class DailyReportsComponent implements OnInit {
	reportData: any = '';
	reportDisplayColumns = ['registration_date', 'bo_count', 'website_count', 'bpo1_count', 'bpo2_count'];
	reportLength: number = 0;
	isLoading: boolean = false;

	// pagination
	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;

	constructor(private adminService: AdminService) { }

	ngOnInit() {
		this.getReportDetails();
	}

	getReportDetails() {
		this.isLoading = true;
		this.adminService.dailyReportCount('generate-report/').subscribe(res => {
			// console.log('res', res);
			this.reportData = res;
			// console.log(this.reports);

			// this.isLoading = false;
			this.loadDataTable(this.reportData);
		}, error => {
			this.isLoading = false;
		})
	}

	// load data tables..
	loadDataTable(dataSource) {
		this.reportData = new MatTableDataSource<any>(dataSource);
		// bind data sounrce to pagination
		this.reportData.paginator = this.paginator;
		this.reportData.sort = this.sort;
		this.reportLength = this.reportData.data.length;
	}

}
