import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/admin/admin.service';

@Component({
	selector: 'app-bpo-report',
	templateUrl: './bpo-report.component.html',
	styleUrls: ['./bpo-report.component.css']
})
export class BpoReportComponent implements OnInit {

	constructor(private adminService: AdminService) { }

	ngOnInit() {
		this.adminService.downloadBPOReport('download-lc-report-source-only-two/').subscribe(res => {
			console.log('res ', res);
			window.open(res.download_link, '_blank');
		})
	}

}
