import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/admin/admin.service';


@Component({
	selector: 'app-pan-check',
	templateUrl: './pan-check.component.html',
	styleUrls: ['./pan-check.component.css']
})
export class PanCheckComponent implements OnInit {
	panCard: string = '';
	response: string = ''
	responsePanNo: string = '';
	responsePanName: string = '';
	responsePanStatus: string = '';
	errorRes: string = '';
	isLoading:boolean = false;

	constructor(private adminService: AdminService) { }

	ngOnInit() {
	}

	getPanDetails() {
		if (this.panCard.length == 10) {
			this.isLoading = true;
			this.adminService.pangstCheck('verify-pan-prod/', { pan_no: this.panCard }).subscribe(res => {
				this.response = res;
				this.responsePanNo = res.data.pan_number;
				this.responsePanName = res.data.name;
				this.responsePanStatus = res.data.pan_status;
				this.isLoading = false;
			}, error => {
				this.isLoading = false;
			})
		} else {			
			this.errorRes = 'Please enter valid pan card no.'			
		}
	}

}
