module.exports = ".page-wrapper {\r\n    margin-bottom: 100px;\r\n}\r\n\r\n.page-images {\r\n    background: url('timeline-img.png');\r\n    background-position: center;\r\n}\r\n\r\n.success-story-left-wrapper {\r\n    margin-bottom: 100px;\r\n}\r\n\r\n.success-story-left-wrapper h2 {\r\n    font-size: 40px;\r\n    font-weight: 700;\r\n    margin: 0 0 20px 0\r\n}\r\n\r\n.success-story-left-wrapper h2 span {\r\n    display: block;\r\n}\r\n\r\n.success-story-left-wrapper h2 span:nth-child(1) {\r\n    color: #f47d35;\r\n}\r\n\r\n/* .success-story-left-wrapper h2 span:nth-child(2) {\r\n    color: #807f80\r\n} */\r\n\r\n.success-story-left-wrapper p {\r\n    font-size: 14px;\r\n}\r\n\r\n/* right wrapper */\r\n\r\n.left-awards-wrapper {\r\n    width: 100px;\r\n    height: 100%;\r\n    float: left;\r\n    padding-top: 7px;\r\n}\r\n\r\n.left-awards-month {\r\n    font-size: 20px;\r\n    font-weight: 700;\r\n    margin-bottom: 10px;\r\n    color: #f47d35;\r\n    text-transform: uppercase;\r\n}\r\n\r\n.left-awards-year {\r\n    font-size: 15px;\r\n    font-weight: 700;\r\n    margin-bottom: 10px;\r\n    color: #807f80\r\n}\r\n\r\n.right-content-wrapper {\r\n    float: left;\r\n    border-left: 10px solid #807f80;\r\n    width: calc(100% - 100px);\r\n    padding-left: 20px;\r\n    position: relative;\r\n    padding-bottom: 20px;\r\n}\r\n\r\n.right-content-wrapper:after {\r\n    position: absolute;\r\n    top: 8px;\r\n    left: -15px;\r\n    width: 20px;\r\n    height: 20px;\r\n    border-radius: 100%;\r\n    background: #f47d35;\r\n    content: '';\r\n}\r\n\r\n.right-content-wrapper:before {\r\n    position: absolute;\r\n    top: 13px;\r\n    left: -10px;\r\n    width: 10px;\r\n    height: 10px;\r\n    border-radius: 100%;\r\n    background: #ffffff;\r\n    content: '';\r\n    z-index: 1;\r\n}\r\n\r\n.right-content-wrapper .content-title {\r\n    font-size: 30px;\r\n    font-weight: 700;\r\n    margin-bottom: 10px;\r\n    color: #807f80;\r\n    display: inline-block;\r\n    width: 85%;\r\n}\r\n\r\n.right-content-wrapper .content-owner {\r\n    background: red;\r\n    width: 35px;\r\n    height: 35px;\r\n    border-radius: 100%;\r\n    vertical-align: top;\r\n    float: right;\r\n    background: url('testimonial-profile.png') no-repeat;\r\n    background-size: cover;\r\n}\r\n\r\n.right-content-wrapper .content-para {\r\n    font-size: 13px\r\n}\r\n\r\n@media screen and (max-width:1199px) {\r\n    .success-story-left-wrapper h2 {\r\n        font-size: 28px;\r\n    }\r\n    .success-story-left-wrapper p {\r\n        font-size: 12px;\r\n    }\r\n}\r\n\r\n@media screen and (max-width:992px) {\r\n    .success-story-left-wrapper {\r\n        margin-bottom: 45px;\r\n    }\r\n}\r\n\r\n@media screen and (max-width:767px) {\r\n    .page-wrapper {\r\n        margin-bottom: 150px;\r\n    }\r\n    .right-content-wrapper .content-title {\r\n        font-size: 20px;\r\n    }\r\n    .right-content-wrapper .content-para {\r\n        font-size: 12px;\r\n    }\r\n    .right-content-wrapper:after {\r\n        top: 2px;\r\n    }\r\n    .right-content-wrapper:before {\r\n        top: 6px;\r\n    }\r\n    .left-awards-wrapper {\r\n        padding-top: 0;\r\n        width: 75px;\r\n    }\r\n}\r\n\r\n@media screen and (max-width:460px) {\r\n    .right-content-wrapper .content-owner {\r\n        display: none;\r\n    }\r\n    .page-wrapper {\r\n        margin-bottom: 180px;\r\n    }\r\n}"