import { Component, OnInit, ViewChild } from '@angular/core';
import { AdminService } from 'src/app/admin/admin.service';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';


@Component({
	selector: 'app-varification-api-error',
	templateUrl: './varification-api-error.component.html',
	styleUrls: ['./varification-api-error.component.css']
})
export class VarificationApiErrorComponent implements OnInit {

	errorData: any = '';
	errorDisplayColumns = ['lab_contractor', 'document_type', 'id_number', 'reason'];
	errorLength: number = 0;
	isLoading: boolean = false;
	
	// pagination
	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;

	constructor(private adminService: AdminService) { }

	ngOnInit() {
		this.isLoading = true;
		this.adminService.getAPIErrorList('get-failed-verfication-api/').subscribe(res=> {
			console.log('res ', res);
			this.loadDataTable(res);
			this.isLoading = false;
		}, error => {
			this.isLoading = false;
		})
	}

	// load data tables..
	loadDataTable(dataSource) {
		this.errorData = new MatTableDataSource<any>(dataSource);
		// bind data sounrce to pagination
		this.errorData.paginator = this.paginator;
		this.errorData.sort = this.sort;
		this.errorLength = this.errorData.data.length;		
	}

}
