import { BrowserModule, Title } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { TranslateModule } from '@ngx-translate/core';
import { TRANSLATE_MODULE_CONFIG } from './translate.model';

// error logging and http client..
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { HttpErrorInterceptorService, DEFAULT_TIMEOUT } from './http-error-interceptor.service';

/* third party plugins */
import { CarouselModule } from 'ngx-owl-carousel-o';
import { AngularMultiSelectModule } from 'angular2-multiselect-dropdown';
import { MatButtonModule, MatFormFieldModule, MatInputModule, MatIconModule, MatTableModule, MatTooltipModule, MatSelectModule, MatSidenavModule, MatExpansionModule, MatPaginatorModule, MatSortModule, MatTabsModule, MatChipsModule, MatDatepickerModule, MatNativeDateModule, MatProgressSpinnerModule } from '@angular/material';

import { SimplebarAngularModule } from 'simplebar-angular';

/* common imports */
import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { HomeSliderComponent } from './home/home-slider/home-slider.component';
import { HomeSlider1Component } from './home/home-slider1/home-slider1.component';
import { HomeDownloadappComponent } from './home/home-downloadapp/home-downloadapp.component';
import { HomeTestimonialComponent } from './home/home-testimonial/home-testimonial.component';
import { TestimonialComponent } from './home/home-testimonial/testimonial/testimonial.component';
import { HomeClientsComponent } from './home/home-clients/home-clients.component';
import { UnderConstructionComponent } from './under-construction/under-construction.component';
import { OurAppComponent } from './our-app/our-app.component';
import { KeyFeaturesComponent } from './home/key-features/key-features.component';
import { PopUpComponent } from './pop-up/pop-up.component';
import { FileValueAccessorDirective } from './labour-contractor-registration/file-value-accessor.directive';

/* route module */
import { AppRoutingModule, routingComponent } from './app-routing-module';
import { SEOServiceService } from './seoservice.service';
import { ScrollTopComponent } from './scroll-top/scroll-top.component';



@NgModule({
	declarations: [
		// common component imports
		AppComponent,
		HeaderComponent,
		FooterComponent,
		HomeSliderComponent,
		HomeSlider1Component,
		HomeDownloadappComponent,
		HomeTestimonialComponent,
		TestimonialComponent,
		HomeClientsComponent,
		UnderConstructionComponent,
		OurAppComponent,
		KeyFeaturesComponent,
		PopUpComponent,
		FileValueAccessorDirective,

		// routing component (which includes all routing components)
		routingComponent,

		ScrollTopComponent
	],
	imports: [
		BrowserModule,
		CarouselModule,
		BrowserAnimationsModule,
		AppRoutingModule,
		FormsModule,
		HttpClientModule,
		AngularMultiSelectModule,
		TranslateModule.forRoot(TRANSLATE_MODULE_CONFIG),

		//Material Module..
		MatButtonModule,
		MatFormFieldModule,
		MatInputModule,
		MatIconModule,
		MatTableModule,
		MatTooltipModule,
		MatSelectModule,
		MatSidenavModule,
		MatExpansionModule,
		MatPaginatorModule,
		MatSortModule,
		MatTabsModule,
		MatChipsModule,
		MatDatepickerModule,
		MatNativeDateModule,
		MatProgressSpinnerModule,

		// scroll bar..
		SimplebarAngularModule
	],
	providers: [
		{
			provide: HTTP_INTERCEPTORS,
			useClass: HttpErrorInterceptorService,
			multi: true
		},
		{ provide: DEFAULT_TIMEOUT, useValue: 45000 },
		Title,
		SEOServiceService
	],
	bootstrap: [AppComponent]
})
export class AppModule { }
