import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { AdminAuthService } from './admin-auth.service';
import { UserModel } from './dashboard/user/user.model';
import { MatTableDataSource } from '@angular/material';

@Injectable({
	providedIn: 'root'
})

export class AdminService {
	private captechBaseURL = environment.captechBaseURL;
	// set bearer token..
	private bearerToken = {
		headers: new HttpHeaders({
			'Content-Type': 'application/json',
			'Authorization': 'Bearer ' + this.authService.getAccessToken()
		})
	}

	constructor(
		private http: HttpClient,
		private authService: AdminAuthService
	) { }

	// get token from auth-login api.. passing static token
	authenticateUser(url, email, password): Observable<any> {
		let header = new HttpHeaders().set('Authorization', 'ab2dc22a-ba9d-11ea-b3de-0242ac130004');
		return this.http.post(this.captechBaseURL + url, { email, password }, { headers: header });
	}

	// get sub contractor list..
	getSubContractorList(para): Observable<any> {
		this.checkExpirationTimeForAPI()
		
		return this.http.get(this.captechBaseURL + para, this.bearerToken);
	}

	// get sub contractor detail..
	getSubContractorDetails(url): Observable<any> {
		this.checkExpirationTimeForAPI()

		return this.http.get(this.captechBaseURL + url, this.bearerToken);
	}

	// add user..
	addUser(url, body):Observable<UserModel> {
		this.checkExpirationTimeForAPI();

		console.log(' this.bearerToken ', this.bearerToken);
		return this.http.post<UserModel>(this.captechBaseURL + url, body, this.bearerToken);
	}

	// view user..
	getUserList(url): Observable<any>{
		this.checkExpirationTimeForAPI();

		return this.http.get(this.captechBaseURL + url, this.bearerToken);
	}

	deleteUser(url){
		this.checkExpirationTimeForAPI();

		return this.http.delete(this.captechBaseURL + url, this.bearerToken);
	}
	
	// calculate time diffrence based on payload value and current value
	checkExpirationTimeForAPI() {
		// debugger;
		let lastExpiryDate = this.authService.getUserPayload().exp;
		let expiryDate = new Date(lastExpiryDate * 1000);
		let currentDate = new Date();

		var diff1 = expiryDate.getTime() - currentDate.getTime();
		var minutesDiff = Math.floor((diff1 / 1000) / 60);

		console.log('minutes ', minutesDiff);

		// debugger;		
		if (minutesDiff > 2) {
			return true;
		} else if (minutesDiff <= 2 && minutesDiff > 0) {
			this.getRefreshToke('refresh-token/').subscribe(res => {
				console.log('refresh-token/ ', res);
				this.authService.setTokenInSession(res.access_token, '');
				return true;
			})
		} else {
			this.authService.clearSession();
			return false;
		}
	}

	// get refresh token..
	getRefreshToke(tokenUrl): Observable<any> {
		let bearerToken = {
			headers: new HttpHeaders({
				'Content-Type': 'application/json',
				'Authorization': 'Bearer ' + this.authService.getRefreshToken()
			})
		}		
		console.log('this.bearerToken ', this.bearerToken);
		console.log('this.getRefreshToken() ', this.authService.getRefreshToken());
		return this.http.post(this.captechBaseURL + tokenUrl, '', bearerToken);
	}

	// backofice pan check	
	pangstCheck(url, body): Observable<any> {
		let header = new HttpHeaders().set('Authorization', 'ab2dc22a-ba9d-11ea-b3de-0242ac130004');
		return this.http.post('https://registration.cap-tech.in/' + url, body, { headers: header });
	}

	// get API error list..
	getAPIErrorList(url): Observable<any> {
		this.checkExpirationTimeForAPI();

		return this.http.get(this.captechBaseURL + url, this.bearerToken);
	}

	// manage single user..
	getSingleUser(url): Observable<any> {
		this.checkExpirationTimeForAPI();

		return this.http.get(this.captechBaseURL + url, this.bearerToken);
	}

	// update single user..
	patchUser(url, body): Observable<any> {
		this.checkExpirationTimeForAPI();

		return this.http.patch(this.captechBaseURL + url, body, this.bearerToken);
	}

	// download excel report..
	downloadExcel(url):Observable<any> {
		this.checkExpirationTimeForAPI();
		
		return this.http.get(this.captechBaseURL + url, this.bearerToken);
	}

	// bpo count report..
	dailyReportCount(url): Observable<any> {
		this.checkExpirationTimeForAPI();
		
		return this.http.get(this.captechBaseURL + url, this.bearerToken);
	}

	// bpo count report
	downloadBPOReport(url): Observable<any> {
		let header = new HttpHeaders().set('Authorization', 'ab2dc22a-ba9d-11ea-b3de-0242ac130004');
		return this.http.get(this.captechBaseURL + url, { headers: header });
	}
}
