import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { filter, map, mergeMap } from 'rxjs/operators';
import { SEOServiceService } from './seoservice.service';

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.css'],
	providers: [ SEOServiceService ]
})
export class AppComponent implements OnInit {
	constructor(private router: Router, private titleService: Title, private activatedRoute: ActivatedRoute, private seoService: SEOServiceService) { }

	ngOnInit() {		
		this.router.events.pipe(
			filter((event) => event instanceof NavigationEnd),
			map(() => this.activatedRoute),
			map((route) => {
				while (route.firstChild) route = route.firstChild;
				return route;
			}),
			filter((route) => route.outlet === 'primary'),
			mergeMap((route) => route.data)
		).subscribe((event) => {
			const meteObj = {
				description: event.description,
				ogTitle: event.ogTitle,
				ogDescription:  event.ogDescription
			}
			this.seoService.updateSEODetails(event.title, meteObj);
		});
	}
}