import { Component, OnInit } from '@angular/core';
import { AdminAuthService } from '../admin-auth.service';
import { Router } from '@angular/router';

@Component({
	selector: 'app-dashboard',
	templateUrl: './dashboard.component.html',
	styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
	isAuthorizedUser: boolean = false;
	defaultOpenPanel: boolean = false;
	userDetails:userPayload;

	constructor(
		private authService: AdminAuthService,
		private router: Router
	) { }

	ngOnInit() {
		// check for super user..
		this.userDetails = this.authService.getUserPayload();
		if (this.userDetails.role == 'super_user') {
			this.isAuthorizedUser = true;
		}

		console.log('userPayload ', this.userDetails);

		// default open panel..
		if (this.router.url == '/sub-contractors/successfull') {
			this.defaultOpenPanel = true;
		}

		//this.authService.checkExpirationTimeForAPI();
	}

	logOutUser() {
		this.router.navigate(['logout']);
	}
}


export interface userPayload {
	email: string
	exp: number
	id: number
	name: string
	role: string
}