import { Component, OnInit, ViewChild } from '@angular/core';
import { AdminService } from '../../../admin.service';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';


@Component({
	selector: 'app-errors',
	templateUrl: './exception-errors.component.html',
	styleUrls: ['./exception-errors.component.css']
})
export class ExceptionErrorsComponent implements OnInit {
	errorData: any = '';
	errorDisplayColumns = ['api_url', 'http_status', 'error_message', 'file_name'];
	errorLength: number = 0;
	isLoading: boolean = false;

	// pagination
	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;

	constructor(private adminService: AdminService) { }

	ngOnInit() {
		this.isLoading = true;
		this.adminService.getAPIErrorList('get-exception-errors/').subscribe(res=> {
			console.log('res ', res);
			this.loadDataTable(res);
			this.isLoading = false;
		}, error => {
			this.isLoading = false;
		})
	}

	// load data tables..
	loadDataTable(dataSource) {
		this.errorData = new MatTableDataSource<any>(dataSource);
		// bind data sounrce to pagination
		this.errorData.paginator = this.paginator;
		this.errorData.sort = this.sort;
		this.errorLength = this.errorData.data.length;		
	}
}
