import { Injectable } from '@angular/core';
import * as jwt_decode from "jwt-decode";
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
	providedIn: 'root'
})
export class AdminAuthService {
	private captechBaseURL = environment.captechBaseURL;
	bearerToken = {
		// headers: new HttpHeaders({
		// 	'Authorization': 'ab2dc22a-ba9d-11ea-b3de-0242ac130004',
		// })        
		hello: 'Hello'
	};

	constructor(
		private router: Router,
		private http: HttpClient
	) { }

	// set token in session..
	setTokenInSession(access_token, refresh_token) {
		sessionStorage.removeItem('access_token');
		sessionStorage.setItem('access_token', access_token);

		if (refresh_token != null || refresh_token != '') {
			sessionStorage.setItem('refresh_token', refresh_token);
		} else {
			this.clearSession();
		}

		sessionStorage.setItem('user', JSON.stringify(this.getDecodedAccessToken(access_token)));
	}

	// get access token from session..
	getAccessToken() {
		return sessionStorage.getItem('access_token');
	}

	// get access token from session..
	getRefreshToken() {
		return sessionStorage.getItem('refresh_token');
	}

	// get user payload from session..
	getUserPayload() {
		return JSON.parse(sessionStorage.getItem('user'));
	}

	//  clear session storage..
	clearSession() {
		sessionStorage.clear();
		this.router.navigate(['/login']);
	}

	// decode JWT token in to payload..
	getDecodedAccessToken(token: string): any {
		try {
			return jwt_decode(token);
		}
		catch (Error) {
			return null;
		}
	}	
}
