import { Injectable, InjectionToken, Inject } from '@angular/core';
import { HttpEvent, HttpInterceptor, HttpHandler, HttpRequest, HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError, TimeoutError } from 'rxjs';
import { catchError, finalize, map, timeout } from 'rxjs/operators';
import { LabourContractorService } from './labour-contractor-registration/labour-contractor.service';

export const DEFAULT_TIMEOUT = new InjectionToken<number>('defaultTimeout');

@Injectable({
    providedIn: 'root'
})
export class HttpErrorInterceptorService implements HttpInterceptor {

    constructor(@Inject(DEFAULT_TIMEOUT) protected defaultTimeout: number, private labourService: LabourContractorService) { }

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        //const API_TIMEOUT = 10000;

        const timeoutValue = request.headers.get('timeout') || this.defaultTimeout;
        const timeoutValueNumeric = Number(timeoutValue);

        //debugger;
        return next.handle(request)
            .pipe(
                timeout(timeoutValueNumeric),
                map((event: any) => {
                    return event;
                }),
                catchError((errorResponse: HttpErrorResponse) => {
                    //debugger;

                    // let errorMessage = '';
                    // if (errorResponse.error instanceof ErrorEvent) {
                    //     // client-side error
                    //     errorMessage = `Error: ${errorResponse.error.message}`;
                    //     console.log('if errorMessage ', errorMessage);
                    // } else {
                    //     // server-side error
                    //     //errorMessage = `Error Code: ${errorResponse.status}\nMessages: ${errorResponse.message}`;
                    //     // this.testFuntion(errorResponse.message);
                    //     //console.log('Timeout Exception ', errorResponse.message);
                    // }
                    console.log('errorResponse ', errorResponse);
                    let errorRes = {
                        api_url: '',
                        method_name: '',
                        line_no: '',
                        file_name: '',
                        http_status: 0,
                        error_message: ''
                    }

                    if(errorResponse.message != "Timeout has occurred"){
                        errorRes.api_url = errorResponse.url;
                        errorRes.method_name = '';
                        errorRes.line_no= '';
                        errorRes.file_name= 'labour-contractor-registration.component.ts';
                        errorRes.http_status= errorResponse.status;
                        errorRes.error_message= JSON.stringify(errorResponse.error)
                        
                    } else {                        
                        errorRes.api_url = 'timeout';
                        errorRes.method_name = 'timeout';
                        errorRes.line_no = 'timeout';
                        errorRes.file_name = 'labour-contractor-registration.component.ts';
                        errorRes.error_message = 'timeout';                        
                    }

                    this.labourService.postRequest('create-exception-errors/', errorRes).subscribe(res => {
                        console.log('post error ', res)
                    })

                    return throwError(errorResponse);

                })
            )
    }
}